      <div class="redabout_content">
        <h2>{{$t('Ordinary Red Envelopes')}}</h2>
        <p>{{$t('Ordinary Red Envelopes Content')}}</p>
        <h2>{{$t('Lucky Red Envelopes')}}</h2>
        <p>{{$t('Lucky Red Envelopes Content')}}</p>
        <h2>{{$t('The Red Envelope for Account Creation')}}</h2>
        <p>{{$t('The Red Envelope for Account Creation Content')}}</p>
        <h2>{{$t('Service Fee')}}</h2>
        <p>{{$t('Service Fee Content')}}</p>
      </div>
  </div>

</template>

<script>
import Enu from 'enujs';
export default {
    data () {
      return {

      }
    },
    created(){

    },

    methods:{

      goBack(){
        window.history.go(-1);
      }
    },
    computed: {

    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
    @rem: round(1/75*2,6)*1rem;
    .redabout_box{
      width: 100%;
      height: 100%;
      .about_nav{
          width: 100%;
          height: 44*@rem;
          line-height: 44*@rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 0 15*@rem;
          background: #ce2344;
          box-sizing: border-box;
          .about_nav_btn{
            width: 30*@rem;
            height: 44*@rem;
            display: flex;
            align-items: center;
            i{
              color: #fff;
              font-size: 25*@rem;
