                  font-size: 23*@rem;
                }
              }

          }
      }
      .redabout_content{
        padding: 24*@rem 16*@rem;
        box-sizing: border-box;
        h2{
          color: #5d4220;
          font-size: 16*@rem;
          margin-bottom: 12*@rem;
        }
        p{
          color: #5d4220;
          font-size: 14*@rem;
          margin-bottom: 40*@rem;
        }
